.navigation {
  color: @basic-black;
}

.navigation__holder {
  display: flex;
  gap: 5px;
}

.navigation__list {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
  margin: 0;
}

.navigation__link {
  margin: 0 5px;
}

.navigation__input-holder {
  display: none;
}

.navigation__link--profile {
  display: none;
}

.navigation__link--menu {
  top: 4px;
  border: none;
  outline: none;
  background: none;
  padding: 0;
}


@media (min-width: @desktop-width) {
  .navigation {
    padding: 0;
  }

  .navigation__holder {
    display: flex;
    gap: 10px;
  }
  

  .navigation__input-holder {
    display: block;
  }

  .navigation__input {
    padding: 0;
    margin: 0;
    border: none;
    border-bottom: 1px solid @basic-grey;
    width: 248px;
    padding: 7px 0;

    &::placeholder {
      font-family: "Helvetica", "Arial", sans-serif;
      font-size: 18px;
      font-weight: 400;
      line-height: 20px;
      color: @basic-grey;
    }

    &:hover {
      color: @basic-light-grey;
      border-bottom: 1px solid @basic-light-grey;
    }
  }

  .navigation__link {
    margin: 0 10px;

    &:hover svg path {
      stroke: @basic-grey;
    }
  }

  .navigation__link--search {
    margin-left: 0;
  }

  .navigation__link--profile {
    display: block;
  }

  .navigation__link--logo {
    display: none;
  }

  .navigation__link--menu {
    display: none;
  }
}